import React, { Component } from 'react';
import './App.css';
import Head from './Head.js';
import Home from './Dashboards/Home.js';
import PipelineVelocity from './Dashboards/PipelineVelocity.js';
import PipelineStages from './Dashboards/PipelineStages.js';
import AllKPIs from './Dashboards/AllKPIs.js';
import {Router, Route, hashHistory} from 'react-router';
import LeadDetails from './Dashboards/Details/LeadDetails.js'



class App extends Component{

  constructor(props)
  {
    super(props);
    this.getSelection = this.getSelection.bind(this);
    this.state = {contents_selection: ""};
  }

  getSelection(arg)
  {
      this.setState({contents_dash: arg});
      console.log("Contents_Dash: " + this.state.contents_dash);
  }
  render()
  {
    return(
      <div>
      <Head callbackFromParent={this.getSelection}/>
      <Router history={hashHistory}>
        <Route path='/' component={Home}/>
        <Route path='main' component={Home}/>
        <Route path='pstages' component={PipelineStages}/>
        <Route path='pvelocity' component={PipelineVelocity}/>
        <Route path='akpis' component={AllKPIs}/>

      </Router>

    </div>
    );
  }
}


export default App;



