import React, {Component} from 'react';
import $ from 'jquery';
import FontAwesome from 'react-fontawesome';
import Tile from './Tile.js';
import Draggable from 'react-draggable';
import { SortablePane, Pane } from 'react-sortable-pane';

class Home extends Component{

  
    constructor(props)
    {
      super(props);
      this.state = {len: "0"};
      this.state = {tiles: "0"};  
      this.state = {isadmin: false};
      this.state = {tilesorder: [0]};
      this.updateDashboard = this.updateDashboard.bind(this);
      this.updateChange = this.updateChange.bind(this);
    }

   
    updateChange(newpane)
    {
          var len = newpane.length;
          console.log(len);
          var order = [];
          var jsontowrite = [];
          jsontowrite.push("{");
          for(var i=0;i<len;i++)
          {
              order.push(newpane[i].id);
          }
         console.log(order);
         
    }
    updateDashboard()
    {
        var final_data;
        $.getJSON("./jsonresp", function(data){  

          var length = Object.keys(data).length;
          this.setState({len: length});
          this.setState({tiles: Object.keys(data).map(function(key) {
            return [String(key), data[key]];
          })
        });
          
      }.bind(this));

      $.getJSON("./tilesorder", function(data){  
        
               this.setState({tilesorder: data});
                  
              }.bind(this));

      $.getJSON("./admincheck", function(data){  

                  this.setState({isadmin: data.isadmin});
                                     
              }.bind(this));

       
      var obj = {
                table: []
             };
      
      
    
  }

    componentDidMount()
    {
      this.updateDashboard();
  //    setInterval(this.updateDashboard,1000);
    }

    componentWillUnmount()
    {
      clearInterval(this.updateDashboard,1000);
    }

    render()
    {
        var tiles = [];
       if(this.state.tiles && this.state.tilesorder)
        {
          for(var i=0;i<this.state.len;i++)
          {
              var currentorder = this.state.tilesorder[i];
              if(currentorder!==undefined) 
                tiles.push(<Pane id={i} key={i} width={220} isResizable={false}><Tile key={i} name={this.state.tiles[currentorder][1]['id']} points={this.state.tiles[currentorder][1]['val']} change={this.state.tiles[currentorder][1]['percentage']} footer={this.state.tiles[currentorder][1]['company']}/></Pane>);
          }
        }

       
        var admin_navbar = [];

        if(this.state.isadmin === "true")
        {
            admin_navbar.push(
              <div className="admin-nav">
              <button className="kpitc"><FontAwesome name="plus"></FontAwesome> KPI</button>
              <button className="kpitc"><FontAwesome name="plus"></FontAwesome> Target</button>
              <button className="kpitc"><FontAwesome name="plus"></FontAwesome> Contest</button>
             
              <button className="kpitcright">More <FontAwesome name="caret-down"></FontAwesome></button>
                <button className="kpitcright"><FontAwesome name="filter"></FontAwesome> Filter</button>
                </div>
            );
        }


        if(this.state.len2)
        {
              

        }
       
          return(
            
            <div className="page-body">
            <div className='page-title'>Home
              {admin_navbar}
              </div>
              <div className="tiles-top">
              <SortablePane direction="horizontal" margin={20} zIndex={0} onOrderChange={(old,newpane) => this.updateChange(newpane)} >
                {tiles}
             </SortablePane>
            </div>
            
            </div>
          );
    }
}
      
   

export default Home;


