import React, { Component } from 'react';
import logo from './sky-motivator-logo.png';
import $ from 'jquery';

class Head extends Component {
    
      constructor(props)
      {
        super(props);
        this.state = {action_dashbord: false};

      
        
        this.update_Dashboard = this.update_Dashboard.bind(this);

      
      //  this.contents_Dashboard = this.contents_Dashboard.bind(this);
     
      }
    
      
      update_Dashboard(arg)
      {
          console.log(arg);
          this.setState({action_dashbord: true});
          this.props.callbackFromParent(arg);
          
      }
      render() {
         return (
          <div className="App">
            <div className="App-header">
             <div className="Bar-main">
                 <a href="#">
               <img src={logo} alt="logo" className="App-logo" width="200px"></img></a>
               <div  className="Bar-listing" onClick={() => this.update_Dashboard("dash")}><span>DASHBOARDS &#x25BE;</span>
                 <div id="dash_contents" className="innercontents">
                  <a href="#main">Home</a>
                  <a href="#pvelocity">Pipeline Velocity</a>
                  <a href="#pstages">Pipeline Stages</a>
                  <a href="#akpis">All KPIs</a>
                  <hr></hr>
                  <a href="#managedash">Manage</a>
                  </div>
                 </div>
              <div className="Bar-listing" onClick={() => this.update_Dashboard("target")}>TARGETS &#x25BE;
              <div id="target_contents" className="innercontents">
              <a href="#scorecard">Scorecard</a>
                  <a href="#personal">Personal</a>
                  <hr></hr>
                  <a href="#alltarget">All</a>
                 </div>
              </div>
               <div className="Bar-listing" onClick={() => this.update_Dashboard("contest")}>CONTESTS  &#x25BE;
               <div id="contest_contents" className="innercontents">
               <a href="#participating">Participating</a>
                  <a href="#observing">Observing</a>
                  <hr></hr>
                  <a href="#allcontests">All</a>
                 </div>
              
               </div>
               <div className="Bar-listing" onClick={() => this.update_Dashboard("tvchannel")}>TV CHANNELS &#x25BE;
               <div id="channel_contents" className="innercontents">
               <a href="#contestchannel">Contest Channel</a>
               <a href="#featuredchannel">Featured Channel</a>
               <a href="#homechannel">Home Channel</a>
               <a href="#pipelinechannel">Pipeline Channel</a>
               <a href="#targetchannel">Target Channel</a>
               <hr></hr>
                  <a href="#managechannels">Manage</a>
                 </div>
              
               </div>
              </div>
            </div>
    </div>
        );
      }
    }
    
export default Head;    


