import React, {Component} from 'react';
import $ from 'jquery';
import { SortablePane, Pane } from 'react-sortable-pane';


class Tile extends Component{
    
      constructor(props)
      {
        super(props);
        this.state = {option1hrefs: "0"};
        this.state = {option1data: "0"};
        this.state = {option2hrefs: "0"};
        this.state = {option2data: "0"};
        this.state = {option1len: "0"};
        this.state = {option2len: "0"};
        $.getJSON("./getdotdetails", function(data){  
          
                  this.setState({option1hrefs: data.option1.hrefs});
                  this.setState({option2hrefs: data.option2.hrefs});
                  this.setState({option1data: data.option1.data});
                  this.setState({option2data: data.option2.data});
                  var len1 = Object.keys(data.option1.hrefs).length;
                  var len2 = Object.keys(data.option2.hrefs).length;
                  this.setState({option1len: len1 });
                  this.setState({option2len: len2});
                  
                }.bind(this));
               
    
      }
    
      
    
      render()
      {
        var dotdetails1 = [];
        var dotdetails2 = [];
    
            if(this.state.option1len)
            {
                for(var i=0; i< this.state.option1len;i++)
                {
                    dotdetails1.push(<a href={this.state.option1hrefs[i]}>{this.state.option1data[i]}</a>);
                }
      
                for(var i=0; i< this.state.option2len;i++)
                {
                    dotdetails2.push(<a href={this.state.option2hrefs[i]}>{this.state.option2data[i]}</a>);
                }
  
            }
              
       
        return (
      
         
          <div className="tile">
              <div className="tile-header">
              <div className="tile-type">KPI</div>
              <div className="dropdot">
              <div className="dotinner">
               {dotdetails1}
                <hr></hr>
             {dotdetails2}
               <hr></hr>
                <span className="dropdownheader"> Add to TV</span>
                <a href="#featuredChannel">Featured Channel</a>
                </div>
                &middot;&middot;&middot;
                
              </div>
              </div>
              <div className="tile-name">{this.props.name}</div>
              <div className="body">
                <div className="kpi-points">{this.props.points}</div>
                <div className="kpi-change">{this.props.change}</div>
              </div>
              <div className="tile-footer">{this.props.footer}</div>
        </div>
      
        );
      }
    }

export default Tile;


